<template>
    <div style="display:flex;align-items:center;justify-content:center">
            <el-form ref="form" :model="form" label-width="80px"
                v-loading="loading"
                element-loading-text=""
                element-loading-background="rgba(0, 0, 0, 0)"
                style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);border:1px solid #E6E6E6;padding: 50px;width:40%;border-radius: 10px;">
            <el-form-item label="">
                <el-select style="width:80%" v-model="form.format" placeholder="" @change="handleFormat">
                  <!-- <el-option label="DWG" value="DWG"></el-option>
                  <el-option label="MapGIS" value="MapGIS"></el-option>
                  <el-option label="Shapefiles" value="Shapefiles"></el-option> 
                  <el-option label="KML/KMZ" value="KML/KMZ"></el-option> -->
                  <el-option label="Excel" value="Excel"></el-option>
                  <el-option label="Access MDB" value="Access MDB"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input style="width:80%" v-model="form.fileName" placeholder="" :disabled = this.form.fileList[0]?true:false></el-input>
                <el-upload
                    :accept="acceptType"
                    class="upload-demo"
                    :auto-upload="false"
                    action="#"
                    :on-change="selectResultFile"
                    :file-list="form.fileList">
                    <el-button size="small" >...</el-button>
                </el-upload>
            </el-form-item>
            <el-form-item label="">
                <el-select style="width:80%" v-model="form.type" placeholder="" @change="handleMdlChange">
                  <el-option v-for="item in resultTypeList" :key="item.mdlNamec"  :value="item.mdlName" :label="item.mdlNamec"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit"></el-button>
                <el-button @click="removeForm"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import * as XLSX from 'xlsx/xlsx.mjs'
import DictionaryMana from '@/api/grpcImportApi/DictionaryManager';
import DbMana from '@/api/grpcImportApi/DBManager'

export default {
   data() {
      return {
        form: {
          format: '',
          fileName: '',
          type: '',
          fileList:[],
        },
        mdbId:null,
        loading:false,
        // excelResults:[], //excel
        // mdbResults:[],  //mdb
        resultTypeList:[],  //getMdlInfoList
        databases:[], //listDatabases
      }
   },
   computed:{
    acceptType(){
        if(this.form.format === 'Access MDB'){
            return '.mdb'
        }
        else if(this.form.format === 'Excel'){
            return '.xls,.xlsx'
        }
        else {
            return ''
        }
    }
   },
   methods:{
    // 
    handleFormat(){
        this.form.fileList = [];
        this.form.fileName = '';
    },
    // getTableInfoList
    async handleMdlChange(mdlName) {
        console.log("Selected value:", mdlName);
        let res = await DictionaryMana.getTableInfoList(mdlName);
        this.$store.commit('dataparse/setTargetTables',res);
    },

    //(fileList)
    async selectResultFile(file){
        //file
        this.form.fileList.splice(0, this.form.fileList.length, file); 
        //
        const fileName = this.form.fileList[0].name;
        const extractedFileName = fileName.substring(0, fileName.lastIndexOf('.')).replace(/\./g, ''); 
        this.form.fileName = extractedFileName;
        //
        console.log("filelist",this.form.fileList);
        if(this.form.format === ''){
            this.$message({
                type:'warning',
                message:'',
            })
            this.form.fileList = [];
            this.form.fileName = '';
        };
    },
    //
    async onSubmit() {
        console.log('submit!');
        if( !this.form.type || !this.form.format || !this.form.fileList[0]){
            this.$message({
                type:'error',
                message:''
            })
        }else{
            if(this.form.format === 'Access MDB'){
                this.loading = true;
                try{
                    this.mdbId = await DbMana.addAccessDataBase(this.form.fileName,this.form.fileList[0]);
                    this.$store.commit('dataparse/setMdbId',this.mdbId);
                    let mdbResults = await DictionaryMana.getTableInfoListFromDB(this.mdbId);
                    this.$store.commit('dataparse/setFileType',this.form.format);
                    this.$store.commit('dataparse/setMyTables',mdbResults);
                    this.loading =false;
                    this.$message({
                        type:"success",
                        message:``
                    })
                    this.$router.push('/dataMate');
                }catch(err){
                    this.loading =false;
                    this.$message({
                        type:"error",
                        message:`${err}`
                    })
                }
            };
            if(this.form.format === 'Excel'){
                this.loading = true;
                await this.handleExcel(this.form.fileList[0]);
                this.loading = false; 
            }
        } 
    },
   //excel
   async handleExcel(ev){
      let file = ev.raw;
      console.log(file)
      if(!file){
        console.log("")
        return ;
      }else{
        let data = await this.readFile(file);
        let workbook = XLSX.read(data,{ type: "binary"});//
        console.log(':')
        console.log(workbook)
        let result = [];
        //json
        for (let i = 0; i < workbook.SheetNames.length; i++) {
            let sheetName = workbook.SheetNames[i];
            let worksheet = workbook.Sheets[sheetName];
            result[i] = {
                data: XLSX.utils.sheet_to_json(worksheet), // 
                name: sheetName // 
            };
        }
        this.$store.commit('dataparse/setFileType',this.form.format);
        this.$store.commit('dataparse/setMyTables',result);  
        this.$router.push('/dataMate');
      }
    },
    //
    readFile(file){
      return new Promise(resolve => {
        let reader = new FileReader();
        reader.readAsBinaryString(file);//
        reader.onload = ev =>{
          resolve(ev.target.result);
        }
      })
    },
    //
    removeForm(){
        this.form = {format: '',region: '',type: '',fileList:[]};
    },
    //()
    async getMdlInfoList(){
       this.resultTypeList = await DictionaryMana.getMdlInfoList();
       console.log("",this.resultTypeList);
    },
    // ()
    async listDatabases(pageNo,pageSize){
      this.databases = await DbMana.listDatabases(pageNo,pageSize);
      console.log("",this.databases);
    },
   },
   mounted(){
    this.getMdlInfoList();
    // this.listDatabases(1,1000);
   },
}
</script>
<style scoped>
.upload-demo {
    display: inline-block;
    margin-left: 5px;
}
:deep(.el-upload-list){
    display: none;
}
</style>

