import * as DicManagerGrpc from "../../assets/proto/dataImport/dictionary_manager_grpc_web_pb"

let client = new DicManagerGrpc.DictionaryManagerPromiseClient(
    process.env.VUE_APP_HZ_NEWS_URL
);
export default {
    //
    async getMdlInfoList(){
        let getMdlInfoListRequest = new DicManagerGrpc.GetMdlInfoListRequest();
        let res = await client.getMdlInfoList(getMdlInfoListRequest,{});
        return res.toObject().mdlInfoList
    },
    //
    async getTableInfoList(mdlName){
        let tableInfoList = new DicManagerGrpc.GetTableInfoListRequest();
        tableInfoList.setIncludeTableDetail(true);
        tableInfoList.setMdlName(mdlName);
        let res = await client.getTableInfoList(tableInfoList,{});
        return res.toObject().tableInfoList
    },
    //mdb
    async getTableInfoListFromDB(id){
        let getTableInfoListFromDBRequest = new DicManagerGrpc.GetTableInfoListFromDBRequest();
        getTableInfoListFromDBRequest.setDbId(id);
        let res = await client.getTableInfoListFromDB(getTableInfoListFromDBRequest,{});
        console.log("",res.toObject())
        return res.toObject().tableInfoList
    }
}